'use client';

import { RotateCw, Tag } from 'lucide-react';
import { useRouter } from 'next/navigation';

import Typography from '@kapoo/global-ui-pack/atom/Typography';
import Img from '@kapoo/global-ui-pack/molecule/Img';
import Button from '@kapoo/headless-ui-pack/components/new-york/Button';

import type { MouseEventHandler } from 'react';

export interface ContentCategoryProps
{
	/**
	 * 카테고리 리스트
	 */
	readonly categories: Record<string, number | undefined>;

	/**
	 * 선택 목록
	 */
	readonly selected?: string[];
}

/**
 * 컨텐츠 카테고리 organism 컴포넌트 반환 메서드
 *
 * @param {ContentCategoryProps} param0: ContentCategoryProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function ContentCategory({ categories, selected = [] }: ContentCategoryProps): React.JSX.Element
{
	const router = useRouter();

	const handleResetClick: MouseEventHandler<HTMLButtonElement> = () =>
	{
		const params = new URLSearchParams(window.location.search);

		params.delete('page');
		params.delete('category');

		router.replace(`?${params.toString()}`, { scroll: false });
	};

	const handleClick: MouseEventHandler<HTMLButtonElement> = ({ currentTarget }) =>
	{
		const key = 'category';
		const { value } = currentTarget;

		const params = new URLSearchParams(window.location.search);

		params.delete('page');

		const category = params.getAll(key);

		if (category.includes(value))
		{
			params.delete(key, value);
		}

		else if (Object.keys(categories).length === selected.length + 1)
		{
			params.delete(key);
		}

		else
		{
			params.append(key, value);
		}

		router.replace(`?${params.toString()}`, { scroll: false });
	};

	return (
		<div className="bg-secondary flex flex-col gap-4 rounded p-4" data-component="ContentCategory">
			<div className="flex items-center gap-2">
				<Tag size={18} />
				<Typography className="font-bold">카테고리</Typography>
			</div>

			<div className="flex flex-wrap gap-1">
				<Button variant="outline" onClick={handleResetClick}>
					<RotateCw />
					전체
				</Button>

				{Object.entries(categories)
					.map(([
						name,
						count
					]) => (
						<Button
							key={name}
							value={name}
							variant={selected.includes(name) ? 'default' : 'outline'}
							onClick={handleClick}
						>
							<Img alt={name} className="rounded-full object-cover" height={20} src={`https://datastore.itcode.dev/blog/category/${name}.png`} width={20} />
							<span>{name}</span>
							<span className="text-muted-foreground">{count}</span>
						</Button>
					))}
			</div>
		</div>
	);
}