'use client';

import { useSearchParams } from 'next/navigation';
import { memo, useCallback, useMemo } from 'react';

import ContentCategory from '../ContentCategory';
import ContentForm from '../ContentForm';
import ContentInfiniteGrid from '../ContentInfiniteGrid';

import type { BlogMarkdownDetailProps, MarkdownHeaderProps } from '../../common/type';

const ContentCategoryMemo = memo(ContentCategory);
const ContentInfiniteGridMemo = memo(ContentInfiniteGrid);

export interface ContentBoxProps
{
	/**
	 * 리스트
	 */
	readonly list?: BlogMarkdownDetailProps<MarkdownHeaderProps>[];
}

/**
 * 컨텐츠 박스 organism 컴포넌트 반환 메서드
 *
 * @param {ContentBoxProps} param0: ContentBoxProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function ContentBox({ list }: ContentBoxProps): React.JSX.Element
{
	const searchParams = useSearchParams();
	const page = Number.parseInt(searchParams.get('page') ?? '1', 10);
	const category = searchParams.getAll('category');
	const keyword = searchParams.get('keyword') ?? undefined;

	const filteredList = useCallback((excludeCategory?: boolean) => list?.filter(({ meta, summary }) =>
	{
		let matched = meta.publish;

		// 카테고리가 하나 이상 선택된 경우
		if (excludeCategory !== true && category.length > 0)
		{
			matched = matched && category.includes(meta.category);
		}

		// 키워드가 하나 이상 입력된 경우
		if (keyword !== undefined)
		{
			matched = matched && summary.toLowerCase()
				.includes(keyword.toLowerCase());
		}

		return matched;
	}), [
		list,
		category,
		keyword
	]);

	const categories = useMemo(() => filteredList(true)
		?.reduce<Record<string, number | undefined>>((acc, cur) =>
		{
			const current = acc[cur.meta.category] ?? 0;

			acc[cur.meta.category] = current + 1;

			return acc;
		}, {}), [ filteredList ]);

	return (
		<div className="flex w-full flex-col gap-10" data-component="ContentBox">
			<ContentForm defaultValue={keyword} />

			{categories && <ContentCategoryMemo categories={categories} selected={category} />}

			<ContentInfiniteGridMemo list={filteredList()} page={page} pageSize={6} />
		</div>
	);
}